  componentDidMount(){
    this._isMounted = true
    this.getAdress()
    //
     if(this._isMounted){
       const { id,curFields } = this.props
       this.setState({
         id,
         curFields
       },()=>{
         this.props.form.resetFields()
         let formData = {}
         if(this.state.curFields.build_time){
           formData = Object.assign({},this.state.curFields,{build_time: moment(this.state.curFields.build_time,'YYYY-MM')})
         }else{
           formData = _.cloneDeep(this.state.curFields)
         }
         this.props.form.setFieldsValue(formData)
       })
     }
  }
  //
  getAdress = ()=>{
      const digui = (arr)=>{
        const cityList = []
        arr.map((item,index)=>{
          let curList = {}
          curList.value = item.n
          curList.label = item.n
          if(item.c){
            curList.children = digui(item.c)
          }
          cityList.push(curList)
        })
        return cityList
      }
      const adressList = digui(cityList)
      if(this._isMounted){
        this.setState({
          adressList
        })
      }
  }
  //
  handleSubmit = e => {
    e.preventDefault()
    this.props.form.validateFieldsAndScroll((err, fieldsValue) => {
      let fields = {}
      if (!err) {
        const values = {
          ...fieldsValue,
          'build_time':fieldsValue['build_time']?fieldsValue['build_time'].format('YYYY-MM'):'',
        }
        fields = values
        if(this._isMounted){
          this.setState({
            isSubmiting: true
          })
        }
        wyAxiosPost('Technology/saveTechnology',{id:this.state.id,info:fields},(result)=>{
          const responseData = result.data.msg
          if(responseData.status === 1){
            this.props.getTotal()
            this.props.closeDrawer()
            message.success(responseData.msg)
            if(this._isMounted){
              this.setState({
                isSubmiting: false
              })
            }
          }else{
            if(this._isMounted){
              this.setState({
                isSubmiting: false
              })
            }
            message.warning(responseData.msg)
          }
        })
      }else{
        const len = Object.keys(err).length
        message.warning(''+len+'')
      }
      return
    })
  }

  //blur
  handleConfirmBlur = e => {
    const { value } = e.target;
    this.setState({ confirmDirty: this.state.confirmDirty || !!value });
  }
